body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dashboard-container {
  display: flex;
  flex-wrap: wrap;
}

.dashboard-card {
  width: 20rem;
}
.mapselect {
  display: flex;
}

.progress-bar {
  height: 75px;
  width: 100%;
  margin: 10px 0;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  border-top: 2px solid #ccc;
  position: fixed;
  bottom: -1%;
  left: 0%;
  background-color: #393838;
  opacity: 1;
  z-index: 9999;
  background: #fff;
}

.progress-bar-hidden {
  display: none;
}

.MuiLinearProgress-root-343 {
  width: 100%;
}

.progress-complete {
  display: flex;
  flex-flow: column;
  align-items: center;
  color: green;
}

.preview-item {
  min-height: 50px;
  padding: 5px 0;
  display: flex;
}

.preview-title {
  font-weight: bold;
}

.records-wrapper {
  display: flex;
  flex-flow: column;
}

